<template>
  <div>

    <el-breadcrumb separator-class="el-icon-arrow-right" class="el-breadcrumb">
      <el-breadcrumb-item :to="{path:'/mainTwo'}">首页</el-breadcrumb-item>
      <el-breadcrumb-item>房产信息管理</el-breadcrumb-item>
      <el-breadcrumb-item>楼栋管理</el-breadcrumb-item>
    </el-breadcrumb>

    <el-card class="el-card">

      <el-row :gutter="10">
        <el-col :span="8">
          <el-input placeholder="请输入楼栋号" v-model="buildAddress">
            <el-button slot="append" icon="el-icon-search" @click="handleSearch"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible=true">添加楼栋</el-button>
        </el-col>
      </el-row>

      <el-table :data="tableData">
        <el-table-column prop="id" label="序号" width="70"></el-table-column>
        <el-table-column prop="buildAddress" label="楼栋单元" width="120"></el-table-column>
        <el-table-column prop="buildArea" label="房型面积" width="120"></el-table-column>
        <el-table-column prop="buildHeight" label="楼层高度" width="120"></el-table-column>
        <el-table-column prop="buildNote" label="备注" width="120"></el-table-column>
        <el-table-column prop="buildState" label="状态" width="120"></el-table-column>
        <el-table-column prop="buildTime" label="发售时间" width="120"></el-table-column>
        <el-table-column label="操作" min-width="170">
          <template slot-scope="scope">
            <el-button type="primary" @click="showReadDialog(scope.row.id)" icon="el-icon-reading">查看</el-button>
            <el-button type="warning" @click="showEditDialog(scope.row.id)" icon="el-icon-edit">修改</el-button>
            <el-button type="danger" icon="el-icon-delete" @click="removeUserById(scope.row.id)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>

      <el-pagination
        background
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-size="pageSize"
        layout="total,prev, pager, next"
        :total="total">
      </el-pagination>
    </el-card>

    <el-dialog
      title="添加房屋"
      :visible.sync="addDialogVisible"
      width="50%"
    >
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="90px" @close="addDialogClosed">
        <el-form-item label="楼栋单元" prop="buildAddress">
          <el-input v-model="addForm.buildAddress"></el-input>
        </el-form-item>
        <el-form-item label="房屋面积" prop="buildArea">
          <el-input v-model="addForm.buildArea"></el-input>
        </el-form-item>
        <el-form-item label="房屋高度" prop="buildHeight">
          <el-input v-model="addForm.buildHeight"></el-input>
        </el-form-item>
        <el-form-item label="备注" prop="buildNote">
          <el-input v-model="addForm.buildNote"></el-input>
        </el-form-item>
        <el-form-item label="房屋状态" prop="buildState">
          <el-input v-model="addForm.buildState"></el-input>
        </el-form-item>
        <el-form-item label="发售时间" prop="buildTime">
          <el-input v-model="addForm.buildTime"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addBuild">确 定</el-button>
      </span>
    </el-dialog>

    <el-dialog
      title="修改房屋"
      :visible.sync="editDialogVisible"
      width="50%"
    >
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="90px" @close="editDialogClosed">
        <el-form-item label="楼栋单元" prop="buildAddress">
          <el-input v-model="editForm.buildAddress"></el-input>
        </el-form-item>
        <el-form-item label="房屋面积" prop="buildArea">
          <el-input v-model="editForm.buildArea"></el-input>
        </el-form-item>
        <el-form-item label="房屋高度" prop="buildHeight">
          <el-input v-model="editForm.buildHeight"></el-input>
        </el-form-item>
        <el-form-item label="备注" prop="buildNote">
          <el-input v-model="editForm.buildNote"></el-input>
        </el-form-item>
        <el-form-item label="房屋状态" prop="buildState">
          <el-input v-model="editForm.buildState"></el-input>
        </el-form-item>
        <el-form-item label="发售时间" prop="buildTime">
          <el-input v-model="editForm.buildTime"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editBuild">确 定</el-button>
      </span>
    </el-dialog>

    <el-dialog
      title="查看"
      :visible.sync="readDialogVisible"
      width="40%"
    >
      <el-form :model="readForm" ref="readFormRef" label-width="90px">
        <el-form-item label="楼栋单元" prop="buildAddress">
          <el-input v-model="readForm.buildAddress" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="房屋面积" prop="buildArea">
          <el-input v-model="readForm.buildArea" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="房屋高度" prop="buildHeight">
          <el-input v-model="readForm.buildHeight" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="备注" prop="buildNote">
          <el-input v-model="readForm.buildNote" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="房屋状态" prop="buildState">
          <el-input v-model="readForm.buildState" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="发售时间" prop="buildTime">
          <el-input v-model="readForm.buildTime" :disabled="true"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="readDialogVisible = false">关  闭</el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
  export default {
    name: "structureManageTwo",
    data() {
      return {
        pageSize: 8,
        currentPage: 1,
        tableData: {
          id:"",
          buildAddress:"",
          buildArea:"",
          buildHeight:"",
          buildNote:"",
          buildState:"",
          buildTime:""
        },
        total: 0,
        buildAddress: "",
        addDialogVisible: false,
        addForm:{
          buildAddress:"",
          buildArea:"",
          buildHeight:"",
          buildNote:"",
          buildState:"",
          buildTime:""
        },
        addFormRules:{
          buildAddress:[
            {required: true, message: '地址不可为空', trigger: 'blur'},
            {min:3,max:20,message: '面积长度在2~20个字符之间',trigger:'blur'}
          ],
          buildArea:[
            {required: true, message: '面积不可为空', trigger: 'blur'}
          ],
          buildHeight:[
            {required: true, message: '高度不可为空', trigger: 'blur'}
          ],
          buildNote:[
            {required: true, message: '备注不可为空', trigger: 'blur'}
          ],
          buildState:[
            {required: true, message: '状态不可为空', trigger: 'blur'}
          ],
          buildTime:[
            {required: true, message: '时间不可为空', trigger: 'blur'}
          ]
        },
        editDialogVisible:false,
        editForm:{
          id:"",
          buildAddress:"",
          buildArea:"",
          buildHeight:"",
          buildNote:"",
          buildState:"",
          buildTime:""
        },
        editFormRules:{
          buildAddress:[
            {required: true, message: '地址不可为空', trigger: 'blur'},
            {min:3,max:20,message: '面积长度在2~20个字符之间',trigger:'blur'}
          ],
          buildArea:[
            {required: true, message: '面积不可为空', trigger: 'blur'}
          ],
          buildHeight:[
            {required: true, message: '高度不可为空', trigger: 'blur'}
          ],
          buildNote:[
            {required: true, message: '备注不可为空', trigger: 'blur'}
          ],
          buildState:[
            {required: true, message: '状态不可为空', trigger: 'blur'}
          ],
          buildTime:[
            {required: true, message: '时间不可为空', trigger: 'blur'}
          ]
        },
        readDialogVisible:false,
        readForm:{
          id:"",
          buildAddress:"",
          buildArea:"",
          buildHeight:"",
          buildNote:"",
          buildState:"",
          buildTime:""
        }
      }
    },
    created() {
      this.queryBuildList()
    },
    methods: {
      queryBuildList() {
        this.$http.get('build/queryBuild',
          {params: {currentPage: this.currentPage, pageSize: this.pageSize, buildAddress: this.buildAddress}})
          .then(res => {
            this.total = res.data.total;
            this.tableData = res.data.data;
          });
      },
      handleCurrentChange(currentPage) {
        this.currentPage = currentPage
        this.queryBuildList()
      },
      handleSearch() {
        this.currentPage = 1
        this.queryBuildList()
      },
      addDialogClosed(){
        this.$refs.addFormRef.resetFields()
      },
      addBuild(){
        this.$refs.addFormRef.validate(async valid=>{
          if(!valid) return
          const {data:res}=await this.$http.post("build/addBuild",this.addForm)
          if(res.code!==200){
            this.$message.error("添加楼栋信息失败,请检查楼栋信息")
          }else {
            this.$message.success("添加楼栋信息成功")
            this.addDialogVisible=false
            this.queryBuildList()
          }
        })
      },
      showEditDialog(id){
        this.editDialogVisible =true
        this.$http.get('build/getBuildById',{params:{id:id}})
          .then(res => {
            this.editForm = res.data;
          });
      },
      editDialogClosed(){
        this.$refs.editFormRef.resetFields()
      },
      editBuild(){
        this.$refs.editFormRef.validate(async valid =>{
          if(!valid) return
          const {data:res}=await this.$http.post("build/editBuild",this.editForm)
          if(res.code!==200){
            this.$message.error("修改楼栋信息失败,请检查楼栋信息")
          }else {
            this.$message.success("修改楼栋信息成功")
            this.editDialogVisible=false
            this.queryBuildList()
          }
        })
      },
      removeUserById(id){
        this.$confirm('此操作将永久删除, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$http.get('build/removeBuildById',{params:{id:id}})
          this.queryBuildList()
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      showReadDialog(id){
        this.readDialogVisible =true
        this.$http.get('build/getBuildById',{params:{id:id}})
          .then(res => {
            this.readForm = res.data;
          });
      },
    }
  }
</script>

<style scoped>

  .el-breadcrumb {
    margin-bottom: 15px;
    font-size: 18px;
  }

  .el-card {
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15);
  }
</style>



